@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

body {
  font-family: 'Playfair Display', serif;
  line-height: 1;
}

.header {
  padding-top: 38px;
  margin-bottom: 45px;
}

.header .wrapper {
  display: flex;
}

.wrapper {
  max-width: 1222px;
  margin: 0 auto;
}

.link {
  color: #333333;
  font-family: "League Spartan";
  font-size: 9px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 5.4px;
  margin-bottom: 10px;
  text-decoration: none;
  position: relative;
  display: inline-flex;
}

.link:after {
  position: absolute;
  content: '';
  display: block;
  bottom: -10px;
  left: 0;
  width: calc(100% - 5px);
  height: 2px;
  background-color: #5da8ef;
  opacity: 0.7;
}

.h2 {
  color: #333333;
  font-size: 42px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.42px;
  padding-bottom: 10px;
  position: relative;
}

.h2:after {
  position: absolute;
  content: '';
  display: block;
  bottom: 0;
  left: 0;
  width: 23px;
  height: 2px;
  background-color: #5da8ef;
}

.logo {
  width: 16px;
  height: auto;
  margin-right: 190px;
  display: block;
}

.nav {
  display: flex;
  padding: 22px 0 14px 0;
}

.nav__item {
  opacity: 0.9;
  color: #0b0c11;
  font-family: "League Spartan";
  font-size: 9px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 5.58px;
  text-decoration: none;
  margin-right: 45px;
}

.nav__item:last-child {
  margin-right: 0;
}

.hero {
  margin-bottom: 240px;
}

.hero .wrapper {
  position: relative;
}

.slider {
  width: 100%;
  position: relative;
}

.slide {
  display: flex;
  position: relative;
  width: 100%;
  padding: 195px 0;
}

.slide__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.slide__name {
  opacity: 0.9;
  color: #5da8ef;
  font-family: "League Spartan";
  font-size: 9px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 5.4px;
  margin-bottom: 25px;
}

.slide__title {
  color: #0b0c11;
  font-size: 62px;
  font-weight: 700;
  margin-bottom: 18px;
}

.slide__description {
  color: #8f8f8f;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.7;
  letter-spacing: 0.35px;
  max-width: 380px;
}

.slide__img {
  max-width: 1016px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}

.slider-nav {
  font-family: "League Spartan";
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 4px;
  color: #ebebeb;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 3px;
  left: 0;
}

.slider-nav__counter {
  margin: 0 18px;
  display: flex;
}

.slider-nav__all {
  color: #262626;
}

.slider-nav__current {
  color: #5da8ef;
}

.slider-nav__current:after {
  content: '/';
}

.slider-nav__arrow {
  width: 11px;
  height: 18px;
  background-image: url("../img/arrow.png");
}

.slider-nav__arrow_right {
  transform: rotate(180deg);
}

.market {
  text-align: center;
  margin-bottom: 173px;
}

.market__sub-title {
  color: #5da8ef;
  font-family: "League Spartan";
  font-size: 9px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 5.4px;
  margin-bottom: 9px;
  text-align: center;
}

.market__title {
  color: #333333;
  font-size: 42px;
  font-weight: 700;
  letter-spacing: 0.42px;
  margin-bottom: 50px;
  text-align: center;
}

.market__content {
  display: flex;
}

.market-item {
  display: flex;
  margin-top: -72px;
  margin-right: 38px;
  align-items: center;
}

.market-item:nth-child(3n) {
  margin-right: -35px;
}

.market-item__img {
  max-width: 250px;
  padding: 0 21px;
  position: relative;
}

.market-item__img:after {
  position: absolute;
  content: '';
  display: block;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 320px;
  background-color: #ececec;
  z-index: -1;
}

.market-item__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 40px;
  margin-left: -35px;
}

.market-item__category {
  color: #7a7a7a;
  font-family: 'Poppins', serif;
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2.4px;
  margin-bottom: 10px;
}

.market-item__title {
  color: #333333;
  font-size: 26px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.26px;
  margin-bottom: 38px;
}

.market-item__price {
  color: #ce60a8;
  font-family: 'Poppins', serif;
  font-size: 20px;
  font-weight: 300;
  letter-spacing: -0.1px;
  margin-bottom: 8px;
  align-self: center;
}

.market-item__link {
  color: #333333;
  font-family: "League Spartan";
  font-size: 9px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 5.4px;
  align-self: center;
  text-decoration: none;
}

.sections {
  display: flex;
  justify-content: space-between;
}

.sections .wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

.section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 120px;
}

.section:last-child {
  margin-bottom: 0;
}

.section__content {
  padding-top: 88px;
}

.section__title {
  margin-bottom: 34px;
}

.section__description {
  opacity: 0.65;
  color: #616161;
  font-family: 'Poppins', serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0.35px;
  margin-bottom: 65px;
  max-width: 341px;
}

.section__img {
  max-width: 862px;
}

.section__img img {
  max-width: 100%;
}

.section_center {
  flex-direction: row-reverse;
  justify-content: center;
}

.section_center .section__img {
  max-width: 489px;
}

.section_center .section__content {
  margin: 0 0 0 75px;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("./ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("./fonts/slick.eot");
  src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"), url("./fonts/slick.woff") format("woff"), url("./fonts/slick.ttf") format("truetype"), url("./fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}
